import { createElement as $ } from 'react';
import programmAktuell from './Programm.aktuell.json';
import { siteCss, join } from '../../common/css';

/* NOTE_1 Kursbeginn und Terminliste werden z.Zt. nicht generiert, Auskommentierungen mit Referenz 'cfr. NOTE_1'*/

const programmTable = year => {
    if (year != programmAktuell.jahr)
        console.warn('Wrong year');
    const { programm } = programmAktuell;
    const trows = [];
    for (const item of programm) {
        const { niveau, kurse } = item;
        trows.push(...kurse.map((kurs, i) => {
            const { uhrzeit, kursbeginn } = kurs;
            // const emailSubject = urlencode(`Voranmeldung - ${niveau} - ${uhrzeit}`);
            const tds = [];
            if (i === 0) {
                tds.push($('td', { key: 'niveau', rowSpan: kurse.length }, niveau));
            }
            const { month, day } = parseDayMonth(kursbeginn);
            const date = new Date(year, month, day);
            const alle = Array(10).fill(null).map((_, j) => {
                const reactEl = $('div', { key: j, className: siteCss('ilist', 'small') }, date.toLocaleDateString('de-DE'));
                date.setDate(date.getDate() + 7);
                return reactEl;
            });
            tds.push(
                $('td', { key: 'kurs' }, [
                    uhrzeit,
                    $('br', { key: 'br' }),
                    $('div', { key: 'beginn', className: siteCss('table') }, [
                        /* cfr. NOTE_1 */
                        // `Kursbeginn: ${kursbeginn}`,
                        /* cfr. NOTE_1 */
                        // $('div', { key: 'toggle', className: siteCss('button', 'small', 'tcell'), onClick: toggle }, ALLE_EINBLENDEN),
                    ]),
                    $('div', { key: 'alle', className: join('hidden', siteCss('narrow')) }, alle)
                ])
            );
            return $('tr', { key: niveau + i },
                tds
            );
        }));
    }
    return $('table', { className: siteCss('table') }, [
        $('colgroup', { key: 'colgroup' }, [
            $('col', { key: 'A', span: 1, style: { width: '25%' } }),
            $('col', { key: 'B', span: 1, style: { width: '75%' } })
        ]),
        $('tbody', { key: 'tbody' },
            trows
        )
    ]);
};

const ALLE_EINBLENDEN = "alle Termine";
function toggle(e) {
    /** @type {Node} */
    const btn = e.currentTarget;
    if (btn.textContent == ALLE_EINBLENDEN) {
        btn.textContent = "ausblenden";
        btn.parentNode.nextElementSibling.classList.remove('hidden');
    } else {
        btn.textContent = ALLE_EINBLENDEN;
        btn.parentNode.nextElementSibling.classList.add('hidden');
    }
}

// '12. September' -> 8
function parseMonth(/** @type {string} */s) {
    const monthname = s.substring(s.lastIndexOf(' ') + 1);
    switch (monthname) {
        case 'Januar': return 0;
        case 'Februar': return 1;
        case 'März': return 2;
        case 'April': return 3;
        case 'Mai': return 4;
        case 'Juni': return 5;
        case 'Juli': return 6;
        case 'August': return 7;
        case 'September': return 8;
        case 'Oktober': return 9;
        case 'November': return 10;
        case 'Dezember': return 11;
        default: console.error(`Could not parse month in '${s}'`);
    }
}
// '12. September' -> 12
function parseDay(/** @type {string} */s) {
    return parseInt(s.substring(0, s.indexOf('.')), 10);
}
// '12. September' -> {day: 12, month: 8}
function parseDayMonth(s) {
    return { day: parseDay(s), month: parseMonth(s) };
}
export default programmTable(2018);


