/* const css = (...names) => names.reduce(
    (acc, cur) => cur ? `${acc} nav--${cur}` : acc
); */
export const cssFactory = prefix => (...names) => names
    .filter(name => name)
    .map(name => `${prefix}--${name}`)
    .join(' ');

export const siteCss = cssFactory('site');

export const join = (...classNames) => classNames.join(' ');



